import React, { Component } from "react";
import BoxList from "../List/BoxList";
import TableList from "../List/TableList";
import Detail from "./Detail";
export class Main extends Component {
  //Î™®Îã¨Í∞ôÏùÄ Í≤ΩÏö∞Ïóî Íµ≥Ïù¥ Ïä§ÌÜ†Ïñ¥ Ïù¥Ïö©ÌïòÏßÄ ÏïäÍ≥† Ïª¥Ìè¨ÎÑåÌä∏ Ïïà Ïä§ÌÖåÏù¥Ìä∏Îßå Ïù¥Ïö©Ìï¥ÎèÑ Îê†ÎìØ
  state = {
    modalData: "",
    edit: false,
    open: false,
  };
  handleDetail = (id) => {
    let data = this.props.data.filter((m) => m.id === id);
    this.setState({ modalData: data });
    this.setState({ edit: true });
    this.setState({ open: true });
  };
  handleClose = () => {
    this.setState({ edit: false });
    this.setState({ open: false });
  };
  handleDelete = (e) => {
    this.props.handleDelete(e.id);
  };
  handleCart = (e) => {
    this.props.handleCart(e.id);
    this.handleClose();
  };
  render() {
    let data = this.props.data;
    console.log("mainÏóêÏÑú Îç∞Ïù¥ÌÑ∞");
    console.log(data);
    return (
      <div>
        <div className="boxLists">
          <BoxList
            data={data}
            onLike={function (e) {
              console.log(e.id);
              this.props.handleLike(e.id);
            }.bind(this)}
            onAddCart={function (e) {
              this.props.handleCart(e.id);
            }.bind(this)}
            onDelete={function (e) {
              this.props.handleDelete(e.id);
            }.bind(this)} //Ïù¥Î†áÍ≤åÎûë ÌòπÏùÄ onDelete={this.handleDelete} Ïù¥Î†áÍ≤å ÎëêÍ∞ÄÏßÄ Î∞©Î≤ï
            onDetail={function (e) {
              this.handleDetail(e.id);
            }.bind(this)}
          />
        </div>
        <div className="tableLists">
          <TableList
            data={data}
            onLike={function (e) {
              console.log(e.id);
              this.props.handleLike(e.id);
            }.bind(this)}
            onAddCart={function (e) {
              this.props.handleCart(e.id);
            }.bind(this)}
            onDelete={this.handleDelete}
            onDetail={function (e) {
              this.handleDetail(e.id);
            }.bind(this)}
          />
          {this.state.edit ? (
            <Detail
              data={this.state.modalData[0]}
              open={this.state.open}
              close={this.handleClose}
              onAddCart={this.handleCart}
            />
          ) : null}
        </div>
      </div>
    );
  }
}

export default Main;
